@import url(http://fonts.googleapis.com/css?family=Varela);
/**
 *
 * More info about this architecture can be found here:
 * http://www.sitepoint.com/architecture-sass-project/
 *
 */
/******COLOR******/
/******FONT_SIZES******/
/******Z-INDEXES******/
.border-box {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

.clear {
  clear: both; }

.hidden {
  display: none; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

*, *:before, *:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

body {
  font-family: 'Varela', sans-serif;
  color: #747474; }

h1 {
  font-size: 48px; }

h4 {
  font-size: 18px;
  line-height: 48px; }

p {
  line-height: 24px; }

h1, h4, strong {
  color: #4e4e4e;
  font-weight: 700; }

.links {
  font-size: 13px; }

.right {
  float: right; }

.left {
  float: left; }

.top-bar {
  height: 100px; }
  .top-bar ul {
    width: auto;
    height: auto !important;
    display: inline; }
    .top-bar ul li {
      float: left;
      padding: 0 15px; }

.wrap {
  padding: 30px;
  margin: 0 47px;
  width: auto;
  height: auto; }

.logo-group {
  margin: 0; }

.menu-group {
  position: relative;
  width: auto !important; }
  .menu-group ul {
    padding: 16px 0; }

.header-img {
  background-image: url("../images/h_man.png");
  background-repeat: no-repeat;
  background-position: left center;
  background-size: cover;
  height: 550px; }

.header-text {
  margin-top: 267px;
  max-width: 426px;
  margin-left: 88px; }

.image-group {
  height: 100%;
  background-color: #747474; }

.content {
  padding: 0px 64px;
  display: block; }
  .content:after {
    content: "";
    display: table;
    clear: both; }

img:after {
  content: "";
  display: table;
  clear: both; }

.slider {
  float: left;
  display: block;
  margin-right: 0%;
  width: 66.66667%; }
  .slider:last-child {
    margin-right: 0; }
  .slider .image {
    width: 40%;
    margin-top: 20px;
    float: left; }
    .slider .image:after {
      content: "";
      display: table;
      clear: both; }
    .slider .image .arrows {
      margin: 120px 0; }
  .slider .text {
    width: 50%;
    overflow: hidden;
    padding: 0;
    margin: 80px 20px 0 20px; }
    .slider .text:after {
      content: "";
      display: table;
      clear: both; }
    .slider .text .arrows {
      margin: 80px 0 0 30px;
      transform: scaleX(-1);
      filter: FlipH;
      float: right; }

.links {
  float: left;
  display: block;
  margin-right: 0%;
  width: 25%;
  margin: 80px 0 0 0; }
  .links:last-child {
    margin-right: 0; }

